<template>
  <div class="container mt-3">
    <div class="mt-3">
      <div class="mt-3">
        <h3><i class="fas fa-users"></i> Proveedores</h3>
      </div>
  
      <div>
            <b-row class="my-2">
                <b-col md="4">
                    <b-form-group label="Filtrar"
                                label-cols-sm="1"
                                label-cols-md="2"
                                label-size="sm"
                                label-for="filterInput">
                        <b-input-group size="sm">
                            <b-form-input v-model="filter"
                                            type="search"
                                            id="filterInput"
                                            placeholder="Escribe el filtro..."
                                            autocomplete="off">
                            </b-form-input>
                            <b-input-group-append>
                                <b-button :disabled="!filter" @click="filter = ''">
                                    <i class="fas fa-times"></i>
                                </b-button>
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
                <b-col md="3">
                    <b-form-group label="Por página"
                                    label-cols-sm="2"
                                    label-cols-md="2"
                                    label-cols-lg="4"
                                    label-size="sm"
                                    label-for="perPageSelect">
                        <b-form-select v-model="perPage"
                                        id="perPageSelect"
                                        size="sm"
                                        :options="pageOptions">
                        </b-form-select>
                    </b-form-group>
                </b-col>
                 <b-col md="3" offset-md="2">
                            <b-button @click="redirect"
                                    variant="primary"
                                    class="btn btn-sm py-1 mt-1 btn-block">
                                <i class="fa fa-plus"></i> Nuevo
                            </b-button>
                 </b-col>
            </b-row>
            <!-- Main table element -->
            <b-table small
                    striped 
                    bordered
                    stacked="md"
                    head-variant="dark"                 
                    :items="items"
                    :fields="fields"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    @filtered="onFiltered"
                    >
                <template v-slot:cell(acciones)="row">
                    <b-button class="mr-1"
                    size="sm"
                    v-b-tooltip.hover
                    variant="primary"
                    title="Contactos"
                     @click="redirectContacto(row.item.idProveedor)">
                      <i class="fa fa-user-friends"></i>
                    </b-button>

                    <b-button class="mr-1"
                    size="sm"
                    v-b-tooltip.hover
                    variant="info"
                    title="Cuentas"
                    @click="redirectCuenta(row.item.idProveedor)">
                      <i class="fas fa-credit-card"></i>
                    </b-button>

                    <b-button class="mr-1"
                    size="sm"
                    v-b-tooltip.hover
                    variant="outline-info"
                    title="Ver detalle"
                    @click="cargarDatosProveedor(row.item.idProveedor)"
                    v-b-modal.modalInfoProveedor>
                      <i class="fas fa-eye"></i>
                    </b-button>
                    
                    <b-button class="mr-1"
                    size="sm"
                    v-b-tooltip.hover
                     variant="outline-info"
                    title="Editar"
                    @click="redirectEditar(row.item.idProveedor)">
                      <i class="fas fa-edit"></i>
                    </b-button>

                    <b-button
                    size="sm"
                    v-b-tooltip.hover
                    class="mr-1"
                    variant="danger"
                    title="Desactivar"
                    @click="desactivar(row.item.idProveedor)"
                    >
                      <i class="fa fa-times"/>
                    </b-button>
                </template>
            </b-table>

            <b-pagination v-model="currentPage"
                    class="mt-1"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    align="center"
                    aria-controls="my-table">
            </b-pagination>


          <b-modal
          id="modalInfoProveedor"
          title="Detalles del proveedor"
          hide-footer
          >
          
            <b-card
            border-variant="light">
              <b-row>
                <b-col sm="5">
                  <b-row>
                    <label> Nombre Comercial:</label>
                  </b-row>
                  <b-row>
                    <label> Tipo:</label>
                  </b-row>
                  <b-row>
                    <label> RFC:</label>
                  </b-row>
                  <b-row>
                    <label> Regimen:</label>
                  </b-row>
                </b-col>
                
                <b-col sm="7">
                  <b-row>
                    <label>{{this.form.NombreComercial}}</label> 
                  </b-row>
                  <b-row>
                    <label>{{this.form.Tipo}}</label>
                  </b-row>
                  <b-row>
                    <label>{{this.form.RFC}}</label> 
                  </b-row>
                  <b-row>
                    <label>{{this.form.Regimen}}</label>
                  </b-row>
                </b-col>
              </b-row>
            </b-card>
          </b-modal>
        </div>
    </div>
  </div>
  
</template>

<script>
import servicioProveedores from "@/services/servicio-proveedores";
export default {
  data() {
    return {
      rutaNuevo: "proveedores/registro/",
      rutaCuentas: "proveedores/cuentas/",
      rutaContactos: "proveedores/contactos/",
      form:{
        NombreComercial : "",
        RFC : "",
        Tipo : "",
        Regimen : ""
      }, 
      items: [],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      filter: null,
      filterOn: [],
      fields: [
        {
          key: "nombreComercial",
          label: "Nombre comercial",
          sortable: true,
          sortDirection: "desc",
        },
        {
          key: "tipo",
          label: "Tipo",
          sortable: true,
          class: "text-center",
        },
        {
          key: "acciones",
          label: "Acciones",
          sortable: false,
          class: "text-center",
        }
      ],
      permisoEscritura: true,
    };
  },
  components: {
  //  TableSortable,
  },
  async mounted() {
     await this.consultarProveedores();
     await this.CargarRenglones();
  },
  computed:{
     sortOptions() {
        // Create an options list from our fields
        return this.fields
                    .filter((f) => f.sortable)
                    .map((f) => {
                        return { text: f.label, value: f.key };
                    });
        },
  },
  methods: {
       onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },
    redirect() {
            setTimeout(() => {
                this.$router.push({ path: `${this.rutaNuevo}0` });
            }, 500);
        },
    redirectEditar(id) {
            setTimeout(() => {
                this.$router.push({ path: `${this.rutaNuevo}${id}` });
            }, 500);
        },

    redirectCuenta(id) {
          setTimeout(() => {
              this.$router.push({ path: `${this.rutaCuentas}${id}` });
          }, 500);
      },

    redirectContacto(id) {
          setTimeout(() => {
              this.$router.push({ path: `${this.rutaContactos}${id}` });
          }, 500);
      },

      desactivar(id){
        let proveedor = {
          idProveedor : id,
          activado :0
        }
        this.$swal.fire({
          title: "¿Desea eliminar el proveedor?",
          text: "se perderan los datos del proveedor",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si",
          cancelButtonText: "No",
        }).then((result) => {
          if (result.value){
            servicioProveedores.DesactivarProveedor(proveedor).then((r) => {
              this.$swal.fire({
                text: r.message,
                icon: "sucess"
              });
              let this_=this;
              setTimeout(function(){this_.$router.go()}, 1000)
            });
          }
        })
      },
    async cargarDatosProveedor(id){
      this.$loading(true);
    await servicioProveedores
      .proveedores(id)
      .then((r) => {
        
        this.form.NombreComercial = r.data.proveedor[0].nombreComercial
        this.form.Tipo =  r.data.proveedor[0].tipo 
        this.form.RFC =  r.data.proveedor[0].rFC
        this.form.Regimen =  r.data.proveedor[0].regimen
        this.$loading(false)
        this.CargarRenglones()
      })
    },

    CargarRenglones() {
            setTimeout(() => {
                this.totalRows = this.items.length;
                console.log(this.totalRows)
            }, 1000);
    },

    async consultarProveedores() {
      this.$loading(true);
     await servicioProveedores
        .proveedores()
        .then((r) => {
          this.items = r.data.proveedor;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },
  },
};
</script>
<style scoped>
</style>
<style>
.hidden_header {
  display: none;
}
</style>